@font-face {
    font-family: 'icons_mobiscroll';
    src: url(data:font/ttf;base64,) format('truetype');
    src: url(data:font/woff;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
}

.mbsc-ic:before {
    font-family: 'icons_mobiscroll';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Icons */
.mbsc-ic-checkmark:before { content: "\f100"; }
.mbsc-ic-close:before { content: "\f101"; }
.mbsc-ic-loop2:before { content: "\f102"; }
.mbsc-ic-minus:before { content: "\f103"; }
.mbsc-ic-plus:before { content: "\f104"; }
